import chartConfigs from '../chartConfigs';
import { ExternalModule, SpecificChartConfig } from '../types';

const registerType = (typeConfig: Record<string, SpecificChartConfig>) => {
  typeConfig &&
    Object.entries(typeConfig).forEach(([name, config]) => {
      chartConfigs[name] = config;
    });
};

const register = (externalModule: ExternalModule) => {
  if (externalModule.moduleType === 'chart') {
    registerType(externalModule.types);
  }
};

export default register;
